.aboutMe-div {
  margin-top: 2.5em;
  gap: 2.5em;
  margin-bottom: 2.5em;
  display: flex;
  flex-direction: column;
}
.aboutMe-div p {
  font-size: 1.5em;
  padding: 0.8em;
}
.aboutMe-div li {
  list-style-type: none;
  padding: 0.4em;
  transition: transform 500ms ease-in-out;
}
.aboutMe-div li:hover {
  transform: translateY(-10%);
}

.terminal {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: 1.5em;
  margin: 0;
}

.skillsText-ul-first {
  height: 7em;
}

.skillsText-ul-last {
  height: 2em;
}/*# sourceMappingURL=AboutMe.css.map */